.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    margin-top:190px;
}

.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-logo p{
    color: #383838;
    font-size: 46px;
    font-weight: 700;
}

.footer-links{
    display: flex;
    list-style: none;
    gap:40px;
    color: #252525;
    font-size: 20px;
}

.footer-links li{
    cursor: pointer;   
}

.footer-social-icon{
    display: flex;
    gap: 20px;
}

.footer-icons-container{
    padding: 10px;
    padding-bottom: 6px;
    background-color: #fbfbfb;
    border: 1px solid #ebebeb;
}

.footer-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}

.footer-copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background-color: #c7c7c7;
}





/* ==============BREAKPOINT============== */
@media (max-width: 475px){

    .footer{
        height: auto;
        overflow: hidden;
        flex-direction: column;
    }



    .footer-logo img{
       width:55px;
    }
    
    .footer-logo p{
        font-size:32px;
    }

    .footer-links{
        flex-direction: column;
        gap:15px;
        font-size: 20px;
    }

    .footer-social-icon{
        display: flex;
        gap:17px;
    }

    .footer-copyright hr{
        width:70%;
        height: 2px;
    }
    
    
    .footer-copyright{
        font-size: 16px;
    }
    

    








}

